import { useSpring, animated } from '@react-spring/web';
import * as React from 'react';

import { extent } from 'd3-array';

import { AxisBottom, AxisLeft } from '@visx/axis';
import { ParentSize } from '@visx/responsive';
import { scaleUtc, scaleLinear } from '@visx/scale';
import { line } from '@visx/shape';

import { useBlogPostContext } from '../../../components/BlogPostContext';

type Item = {
  date: string;
  close: number;
  lower: number;
  middle: number;
  upper: number;
};

export type Props = {
  width: number;
  height: number;
  data: Item[];
};

const MARGIN = { top: 20, right: 30, bottom: 30, left: 40 };

function useAnimatedPath(
  {
    toggle,
  }: {
    toggle?: boolean;
  } = {},
  deps?: React.DependencyList
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
): any {
  const [length, setLength] = React.useState(0);
  const animatedStyle = useSpring({
    strokeDashoffset: toggle ? 0 : length,
    strokeDasharray: length,
  });
  const refPath = React.useRef<SVGPathElement>(null);
  React.useEffect(() => {
    if (refPath.current) {
      const totalLength = refPath.current.getTotalLength();
      // console.log({ totalLength });
      setLength(totalLength);
    }
  }, deps);

  return {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    style: animatedStyle as any,
    ref: refPath,
  };
}

const Content = React.memo((props: Props) => {
  // eslint-disable-next-line
  const { width, height, data } = props;

  const xScale = React.useMemo(
    () =>
      scaleUtc({
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        domain: extent(data, (d) => new Date(d.date)) as any,
        range: [MARGIN.left, width - MARGIN.right],
      }),
    [width, height, data]
  );

  // const xScaleD3 = scaleUtcD3()
  //   .domain(extent(data, (d) => new Date(d.date)) as any)
  //   .range([MARGIN.left, width - MARGIN.right]);

  const yScale = React.useMemo(
    () =>
      scaleLinear({
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        domain: extent(data, (d) => d.upper) as any,
        range: [height - MARGIN.bottom, MARGIN.top],
        nice: true,
      }),
    [width, height, data]
  );

  const createLine = line<Item>()
    .x(({ date }) => xScale(new Date(date)))
    .y(({ close }) => yScale(close));
  // .curve(curveNatural);

  // const yScaleD3 = scaleLinearD3()
  //   .domain(extent(data, (d) => d.upper) as any)
  //   .range([height - MARGIN.bottom, MARGIN.top]);

  // const l = React.useMemo(
  //   () =>
  //     line<Item>({
  //       x: (d) => xScale(new Date(d.date)),
  //       y: (d) => yScale(d.close),
  //     }),
  //   [xScale, yScale, data]
  // );

  const [toggle, setToggle] = React.useState(true);
  // const value = useSpring({
  //   x: 1000,
  //   from: { x: 0 },
  //   // reset: true,
  //   // reverse: true,
  // });
  const { style, ref } = useAnimatedPath({ toggle }, [width]);
  // console.log('Content', { width, height, value });
  return (
    <>
      <button type="button" style={{ border: '1px solid', padding: '5px' }} onClick={() => setToggle((prev) => !prev)}>
        Toggle
      </button>
      <svg width={width} height={height}>
        <animated.path
          style={style}
          ref={ref}
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          // d={l(data) as any}
          d={createLine(data) || ''}
          stroke="steelblue"
          strokeWidth="1.5"
          strokeMiterlimit="1"
          fill="none"
        />
        {/* <animated.path d={l(data) as any} stroke="#333" /> */}
        {/* <Line d={createLine(data) || ''} stroke="#333" /> */}
        {/* <LinePath
          style={style}
          innerRef={ref}
          data={data}
          x={(d) => xScale(new Date(d.date))}
          y={(d) => yScale(d.close)}
          stroke="#333"
        /> */}
        <AxisLeft scale={yScale} left={MARGIN.left} numTicks={width / 80} />
        {/* <AxisLeft scale={yScaleD3} left={MARGIN.left} /> */}
        <AxisBottom scale={xScale} top={height - MARGIN.bottom} numTicks={height / 40} />
        {/* <AxisBottom scale={xScaleD3} top={height - MARGIN.bottom} /> */}
      </svg>
    </>
  );
});

const Chart = React.memo(() => {
  const { data } = useBlogPostContext();
  return (
    <ParentSize debounceTime={300} enableDebounceLeadingCall={false}>
      {({ width }) => <Content width={width} height={300} data={data} />}
    </ParentSize>
  );
});

export default Chart;
